import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Navbar, Nav, Container, Row, NavDropdown } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import SearchBox from './SearchBox'
import { logout } from '../actions/userActions'

function Header() {

    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin

    const dispatch = useDispatch()

    const logoutHandler = () => {
        dispatch(logout())
    }

    return (
        <header>
            <Navbar bg="dark" variant="dark" expand="md" rounded aria-label="Eleventh navbar example">
                <div className="container-fluid">

                    <LinkContainer to='/'>

                        <Navbar.Brand>UberShop</Navbar.Brand>

                    </LinkContainer>
                    <Navbar.Toggle aria-controls="basic-navbar-nav mb-2 mb-md-0" />
                    <Navbar.Collapse id="basic-navbar-nav ">

                        <Nav className="me-auto">
                            <LinkContainer to='/catalog'>
                                <Nav.Link><i className="fa fa-bars"></i></Nav.Link>
                            </LinkContainer>
                            <LinkContainer to='/cart'>
                                <Nav.Link ><i className="fas fa-shopping-cart"></i></Nav.Link>
                            </LinkContainer>

                            {userInfo ? (
                                <NavDropdown title={userInfo.name} id='username'>
                                    <LinkContainer to='/profile'>
                                        <NavDropdown.Item></NavDropdown.Item>
                                    </LinkContainer>

                                    <NavDropdown.Item onClick={logoutHandler}></NavDropdown.Item>

                                </NavDropdown>
                            ) : (
                                <LinkContainer to='/login'>
                                    <Nav.Link><i className="fas fa-user"></i></Nav.Link>
                                </LinkContainer>
                            )}
                            {userInfo && userInfo.isAdmin && (
                                <NavDropdown title='Admin' id='adminmenue'>
                                    <LinkContainer to='/admin/userlist'>
                                        <NavDropdown.Item></NavDropdown.Item>
                                    </LinkContainer>

                                    <LinkContainer to='/admin/productlist'>
                                        <NavDropdown.Item></NavDropdown.Item>
                                    </LinkContainer>

                                    <LinkContainer to='/admin/orderlist'>
                                        <NavDropdown.Item></NavDropdown.Item>
                                    </LinkContainer>

                                </NavDropdown>
                            )}

                            <LinkContainer to='/about'>
                                <Nav.Link ><i className="fa fa-info-circle"></i> </Nav.Link>
                            </LinkContainer>



                        </Nav>
                        <SearchBox />

                    </Navbar.Collapse>
                </div>
            </Navbar>
        </header>
    )
}

export default Header
